import {React} from 'react'
import './university.css'

const University = ({name, state, link, country}) => {
    return(
        <div className="university">
            <div className={`extra-uni1`}></div>
            <div className={`extra-uni2`}></div>
            <p className="university-name"><a target="blank" href={link}>{name} {`${state ? `, ${state}` : ''}`} {`${country ? `, ${country}` : ''}`}</a></p>
        </div>
    )
}

export default University