import { useState, useEffect } from 'react';
import axios from 'axios'
import { FaSearch, FaArrowAltCircleDown } from 'react-icons/fa'
import './App.css';
import University from './university';

function App() {
  const [universities, setUniversities] = useState([])
  const [input, setInput] = useState('')
  const [count, setCount] = useState(20)
  const [condition, setCondition] = useState(true)
  const cancelToken = axios.CancelToken
  const source = cancelToken.source()
  useEffect(() => {
    axios.get('http://universities.hipolabs.com/search?country=India', {
      cancelToken: source.token
    })
      .then(res => {
        setUniversities(res.data)
        setCondition(false)
      })
      .catch(err => {
        console.error(err);
      })
    return () => source.cancel('Last Request Cancelled')
  }, [])

  const extArray = universities.map(value => value["name"])

  const nextArray = universities.filter((value, index) => {
    return !extArray.includes(value.name, index+1)
  })

  const filteredUniversities = nextArray.filter(value => {
    return (value["name"] && value["name"].toLowerCase().includes(input.toLowerCase())) ||
    (value["country"] && value["country"].toLowerCase().includes(input.toLowerCase())) ||
    (value["state-province"] && value["state-province"].toLowerCase().includes(input.toLowerCase()))
  })
  
  filteredUniversities.sort((value1, value2) => {
    return value1.name.toLowerCase() > value2.name.toLowerCase() ? 1 : -1
  })

  const changeInput = e => {
    e.preventDefault()
    setInput(e.target.value)
    setCount(19)
  }
  const more = () => {
    setCount(count + 20)
  }
  
  return (
    <div className="App">
      <div className="search-section">
        <div className="main-heading">
          <h1><a href="/">Search University</a></h1>
        </div>
        <div className="searchbar">
          <input onChange={changeInput} placeholder='Search University' type="text" />
          <span><FaSearch /></span>
        </div>
      </div>

      {condition && <div className="circle"></div>}

      <div className="universities">
        {
        filteredUniversities.length > 0 ? filteredUniversities.map((university, index) => {
            
            return (
              index < count && <University
                key={index}
                name={university["name"]}
                state={university["state-province"]}
                country={university["country"]}
                link={university["web_pages"]}
              />
            )
          }) : !condition && <h1 className='nof'>No University Found</h1>
        }
      </div>

      <div className="more">
        {filteredUniversities.length > 20 && <button onClick={more}><span>More Universities <FaArrowAltCircleDown /></span></button>}
      </div>
    </div>
  );
}

export default App;
